<template>
  <div class="fillinfo">
    <img class="bg" src="~img/information_bg_01.png" />
    <img class="bg" src="~img/information_bg_02.png" />
    <div class="fillinfo__cont">
      <div class="fillinfo__cont--row">
        <div class="item">
          <img src="~img/information_sign_01.png" />
          <input type="text" placeholder="请输入姓名" v-model="username" maxlength="5">
        </div>
        <div class="err"><span v-show="valid.username">*姓名格式不正确</span></div>
      </div>
      <div class="fillinfo__cont--row">
        <div class="item">
          <img src="~img/information_sign_02.png" />
          <Select v-model="inschool" placeholder="请选择是否在校" class="select__picker">
            <Option v-for="item in pickerList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </div>
        <div class="err"><span v-show="valid.inschool">*请选择是否在校</span></div>
      </div>
      <div class="fillinfo__cont--row">
        <div class="item">
          <img src="~img/information_sign_03.png" />
          <input type="text" placeholder="请输入学院名称" v-model="schoolname" maxlength="15">
        </div>
        <div class="err"><span v-show="valid.schoolname">*请输入学院名称</span></div>
      </div>
      <div class="fillinfo__cont--row">
        <div class="item">
          <img src="~img/information_sign_04.png" />
          <input type="text" placeholder="请输入所学专业" v-model="major" maxlength="15">
        </div>
        <div class="err"><span v-show="valid.major">*请输入所学专业</span></div>
      </div>
      <div class="fillinfo__cont--row">
        <div class="item">
          <img src="~img/information_sign_05.png" />
          <input type="text" placeholder="请输入班级" v-model="classname" maxlength="15">
        </div>
        <div class="err"><span v-show="valid.classname">*请输入班级</span></div>
      </div>
      <div class="fillinfo__cont--row">
        <div class="item">
          <img src="~img/information_sign_06.png" />
          <input type="text" placeholder="请输入学号" v-model="studentcode">
        </div>
        <div class="err"><span v-show="valid.studentcode">*请输入学号</span></div>
      </div>
      <div class="fillinfo__cont--btn" @click="submit"></div>
    </div>
  </div>
</template>
<script>
import mixin from '@/mixins/mixins.js';
import { Local } from 'js/utils.js';

export default {
  name: 'fillinfo',
  props: [],
  data() {
    return {
      userid: Local.get('user_id') || '',
      pickerList: [{
          value: '0',
          label: '否'
        },
        {
          value: '1',
          label: '是'
        },
      ],
      username: '',
      inschool: '',
      schoolname: '',
      major: '',
      classname: '',
      studentcode: '',
      valid: {
        username: 0,
        inschool: 0,
        schoolname: 0,
        major: 0,
        classname: 0,
        studentcode: 0,
      }
    };
  },
  mixins: [mixin],
  created() {
      let that = this;
      let phone = Local.get('user_phone');
      if (phone && phone != '') {
        //判断信息是否完善
        that.$api.SelectUserInfo(phone).then(res => {
          if (Number(res.code) == 10000) {
            let data = res.data;

            if (!data.userInfo.phone || data.userInfo.phone == '') {
              that.$Message.success('请先登录');
              Local.clear();
              setTimeout(() => {
                that.$router.push('/login');
              }, 500)
              return;
            }
            
            if (data.userInfo.username && data.userInfo.schoolname && data.userInfo.major && data.userInfo.classname && data.userInfo.studentcode) {
              that.$router.push('/info');
            }
          } else {
            that.$Message.warning(res.msg);
          }
        })
      } else {
        that.$Message.success('请先登录');
        setTimeout(() => {
          that.$router.push('/login');
        }, 500)
      }
  },
  mounted() {

  },
  methods: {
    validData(value) {
      let that = this;
      if (that[value] == '') {
        that.valid[value] = 1;
        return false;
      } else {
        that.valid[value] = 0;
        return true;
      }
    },
    submit() {
      let that = this;
      if (that.validData('username') && that.validData('inschool') && that.validData('schoolname') && that.validData('major') && that.validData('classname') && that.validData('studentcode')) {
        let userobj = {};
        userobj.id = that.userid;
        userobj.username = that.username;
        userobj.inschool = that.inschool;
        userobj.schoolname = that.schoolname;
        userobj.major = that.major;
        userobj.classname = that.classname;
        userobj.studentcode = that.studentcode;
        // console.log(userobj)
        that.$api.UpdateUserInfo(userobj).then(res => {
          // console.log(res)
          if (Number(res.code) == 10000) {
            that.$Message.success('操作成功');
            setTimeout(() => {
              that.$router.push('/info');
            }, 500)
          } else {
            that.$Message.warning(res.msg);
          }
        })
      }
    }
  },
};

</script>
<style lang="scss" scoped>
.fillinfo {
  position: relative;

  img.bg {
    display: block;
    vertical-align: middle;
  }

  &__cont {
    position: absolute;
    left: 50%;
    bottom: 10%;
    transform: translateX(-50%);
    width: 80%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &--row {

      .item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        // border: 1px solid #fff;

        img {
          width: 30px;
          height: 30px;
          flex-shrink: 0;
          margin-right: 10px;
        }

        input,
        select {
          width: 100%;
          height: 41px;
          background: #FFFBF7;
          border-radius: 4px;
          font-size: 16px;
          padding: 0 12.5px;
          border: 0;
          outline: none;

          ::placeholder {
            color: #CEC9CD;
          }
        }

        .select__picker {
          width: 100%;
          height: 41px;

          /deep/ .ivu-select-selection {
            height: 100%;

            .ivu-select-placeholder,
            .ivu-select-selected-value {
              display: block;
              height: 41px;
              line-height: 41px;
              font-size: 16px;
              padding: 0 12.5px;
            }

            .ivu-select-placeholder {
              color: #CEC9CD;
            }

            .ivu-icon {
              font-size: 25px;
              margin-top: -13px;
              right: 10px;

              &.ivu-icon-ios-arrow-down:before {
                content: "\F33D" !important;
                color: #292634;
              }
            }
          }

          /deep/ .ivu-select-dropdown {
            .ivu-select-dropdown-list {
              .ivu-select-item {
                padding: 12.5px;
                font-size: 16px !important;
              }
            }
          }
        }
      }

      .err {
        height: 25px;
        line-height: 25px;
        padding-left: 40px;
        font-size: 13px;
        color: #E84E39;
      }

    }

    &--btn {
      width: auto;
      height: 53.5px;
      margin-top: 10px;
      transition: all .2s ease-in-out 0s;
      background: url('~img/information_btn_tj.png') center center / 100% 100% no-repeat;

      &:active {
        opacity: .5;
        transform: scale(1.1);
      }
    }
  }
}

</style>



